.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  @media screen and (max-width: 376px) {
    .checkoutProduct {
      max-width: 376px;
    }
  }
  
  .checkoutProduct__info {
    padding-left: 20px;
  }
  
  .checkoutProduct__info > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    background-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }
  
  .checkoutProduct__image {
    object-fit: contain;
    width: 180px;
    height: 180px;
  }
  @media screen and (max-width: 376px) {
    .checkoutProduct__Image {
      max-width: 100px;
      max-height: 100px;
    }
  }
  .checkoutProduct__rating {
    display: flex;
  }
  .checkoutProduct__title {
    font-size: 17px;
    font-weight: 800;
  }
  @media screen and (max-width: 376px) {
    .checkoutProduct__title {
      font-size: 12px;
    }
  }
  